import { useState } from 'react';
import { TextField } from '@material-ui/core';
import { useNavigate } from "react-router-dom";

import { useCallback } from "react";
import Particles from "react-particles";
import { loadFull } from "tsparticles";

import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import { noSelect, particlesJsOptions } from './usefulStuff.js';

const theme = createMuiTheme({
	palette: {
		type: 'dark',
		primary: {
			main: '#36D6CA',
		},
	},
});

const LandingPage = props => {
	const navigate = useNavigate();
	const [ value, setValue ] = useState("");
	const [ error, setError ] = useState(false);
	
	const isValidPID = () => /^[0-9a-z]{24}$/.test(value);

	const onEnterPressed = ev => {
		if(ev.key === 'Enter') {  
			ev.preventDefault();
			if(isValidPID()) navigate(`/p/${value}`);
			else setError(true);
		}
	}

	const particlesInit = useCallback(
		async engine => await loadFull(engine),
    	[]
    );

    const particlesLoaded = useCallback(
    	async container => await console.log(container), 
      	[]
    );
	
	return (
		
		<ThemeProvider theme={theme}>
		
			<div style={{zIndex: -1}}>
				<Particles id="tsparticles" init={particlesInit} loaded={particlesLoaded} options={particlesJsOptions} />
			</div>	
			
			<p style={{color: "#ffffff", fontSize: "calc(2.5vw + 1.4rem)", margin: "0 0 1vh 0", ...noSelect }}>
				parqet-deanonymize
			</p>
			<p style={{color: "#ffffff", fontSize: "calc(0.8vw + 0.8rem)", margin: "0 0 200px 0", ...noSelect }}> 
				deanonymize user funds using  Math  
			</p>
			
			<div style={{background: '#26B6AA22', boxShadow: "0 0 10px 10px #26B6AA22", display: "flex", flexDirection: "column", width: "85%", maxWidth: "400px", alignItems: "center" }}>
				<TextField
					label="enter portfolio-ID"
					onChange={ev =>{ setError(false); setValue(ev.target.value);}}
					value={value}
					onKeyPress={onEnterPressed}
					error={error}
					helperText={error ? "this is not a valid Portfolio-ID :(" : ""}			
					style = {{ width: "calc(100% - 20px)", margin: "0px 10px 20px 10px" }}	
				/>

				
				<a style={{color: "#ffffff", cursor: "pointer", ...noSelect }}>  what is the portfolio-ID? </a>
			</div>
		</ThemeProvider>
	);
}

export default LandingPage;
